import "./styles.css";
import React, { useState } from "react";

const preference = {
  javascript: [
    {
      rating: "4/5",
      name: "Eloquent JavaScript"
    },
    { rating: "3.5/5", name: "You Don't Know JS" }
  ],

  fiction: [
    {
      name: "Shiva Trilogy",
      rating: "5/5"
    },
    {
      name: "Harry Potter and the Sorcerer's Stone",
      rating: "4.5/5"
    }
  ],
  business: [
    {
      name: "Never Split the Difference",
      rating: "3.5/5"
    },
    {
      name: "Loonshots",
      rating: "5/5"
    }
  ]
};

const listPreference = Object.keys(preference);

export default function App() {
  const [selectedGenre, setGenre] = useState("javascript");

  function preferenceClickHandler(genre) {
    setGenre(genre);
  }

  return (
    <div className="App">
      <h1>ðŸ“š goodbooks</h1>
      {listPreference.map(function (genre) {
        return (
          <button key={genre} onClick={() => preferenceClickHandler(genre)}>
            {genre}
          </button>
        );
      })}
      <hr />
      <div style={{ textAlign: "left" }}>
        <ul style={{ paddingInlineStart: "0" }}>
          {preference[selectedGenre].map((book) => (
            <li class="bookName" key={book.name}>
              <div style={{ fontSize: "larger" }}> {book.name} </div>
              <div style={{ fontSize: "smaller" }}> {book.rating} </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
